@use './palette' as *;
@use './typography' as *;

html,
body {
  height: 100%;
}

body {
  background: #fff;
  padding: 0;
  margin: 0;
  @include font-family-body();
  @include font-size-body();
}

.ui {
  @include font-family-sui();
}

h1,
h2,
h3,
h4,
h5,
h6,
td,
dl,
ol,
blockquote {
  color: $black;
}

h1 {
  @include font-family-title();
  font-size: 1.7em;
}

h1.category-title {
  font-size: 2em;
}

h2,
h3,
h4 {
  @include font-family-title-light();
}

h2 {
  font-size: 1.6em;
}

h3 {
  font-size: 1.2em;
}

sup,
sub {
  vertical-align: baseline;
  position: relative;
  top: -0.4em;
}

sub {
  top: 0.4em;
}

pre {
  white-space: pre-wrap; /* css-3 */
  white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
  white-space: -o-pre-wrap; /* Opera 7 */
  word-wrap: break-word; /* Internet Explorer 5.5+ */
  margin: 0;
}

/* Elements */

.page-title {
  margin-top: 0;
  font-size: 2em;
  font-weight: normal;
  color: $dark-orange;
}

.group-title {
  border-bottom: 2px solid #eaeaea;
  display: block;
  font-size: 2em;
  font-weight: normal;

  & > i {
    font-size: 1.2em;
    padding-right: 0.2em;
    vertical-align: middle;
  }
}

.groupTitle,
.groupTitleNoBorder,
.groupSubTitle {
  display: block;
  width: 100%;
  font-family: 'Helvetica Neue', Helvetica, Verdana, Arial, sans-serif;
  font-size: 24px;
  font-weight: normal;
  color: #4a4a4a;
  padding: 3px 3px 5px 3px;
  border-bottom: 1px solid #aaa;
  margin-bottom: 1em;
}

td.groupTitle,
td.groupTitleNoBorder,
td.groupSubTitle {
  display: table-cell;
}

.groupTitle > [class*='icon-'],
.groupTitleNoBorder > [class*='icon-'] {
  color: #ccc;
  font-size: 1.2em;
  vertical-align: middle;
}

.reviewingsubtitle {
  font-family: 'Times New Roman', Verdana, Arial, sans-serif;
  font-size: 15pt;
  color: #4e4c46;
  letter-spacing: 1px;
  padding: 3px;
  margin: 10px 0;
}

.groupTitleNoBorder {
  border-bottom: none;
}

.quotation {
  margin: 10px;
  padding-left: 10px;
  color: #777;
  border-left: 4px solid #ddd;
}

/* Quick rules */

.nohtml {
  white-space: pre-line;
}

.overflow {
  overflow: auto;
}

.bold {
  font-weight: bold;
}

.italic {
  font-style: italic;
}

.text-right {
  text-align: right;
}

.text-important {
  color: #111;
}

.text-normal {
  color: #444;
}

.text-error {
  color: $red;
}

.text-not-important {
  color: $light-black;
}

.text-superfluous {
  color: $gray;
}

.text-paper {
  @include font-family-serif();
  text-align: justify;
  text-indent: 2em;

  h3 {
    color: inherit;
    font-family: inherit;
    font-size: 1.1em;
    font-weight: inherit;
    margin: 0 0 1.2em 0;
    text-align: center;
    text-indent: 0;
    text-transform: uppercase;
  }
}

a.small-link {
  color: $light-black;
  font-size: 11px;
}

.mono,
code {
  @include font-family-monospace();
}

textarea.log {
  @extend .mono;
  color: $dark-gray;
  font-size: 0.7em;
}

.preformatted {
  white-space: pre-wrap;
}

.pre-line {
  white-space: pre-line;
}

.underline {
  text-decoration: underline;
}
